export default {
  methods: {
    handleLoad() {
      this.originLoaded = true;
      setTimeout(() => {
        //要等所有的都加载好了以后,占位图再消失
        //不然占位图就会提前消失会出现一下空白
        this.everythingDone = true;
        this.$emit("load");
      }, this.duration);
    }

  },

  data() {
    return {
      originLoaded: false,
      //原图是否加载完成
      everythingDone: false //是否一切都加载完成

    };
  },

  computed: {
    originOpacity() {
      return this.originLoaded ? 1 : 0;
    }

  },
  props: {
    src: {
      type: String,
      required: true
    },
    placeholder: {
      type: String,
      required: true
    },
    duration: {
      type: Number,
      default: 500
    }
  }
};